<template>
  <div class="risk-box">
    <van-dialog v-model="riskShow" show-cancel-button title="风险揭示书" cancelButtonText="我已同意" confirmButtonText="取消" cancelButtonColor="#FF7100" confirmButtonColor="#8C8B8B"	@cancel="openRisk" @confirm="closeRisk">
      <div slot="default" class="risk-content">
        <div class="risk-book">
          <div class="risk-title">尊敬的客户：</div>
          　　本风险揭示书旨在向您充分揭示投资风险，当您进行投资时可能获得较高收益，但同时也存在相应的风险。为了帮助您更好地了解其中风险，根据有关法律法规、规章政策和交易所业务规则等，特提供本《风险揭示书》。请您在入市前务必认真、详细阅读并完全理解本告知书中的全部内容，并对自身的风险理解能力、风险承受能力、风险控制能力以及财务经济状况、投资经验等做出客观判断，自行决定是否参与。<br/>
          　　请仔细阅读以下内容，在您选择勾选确认知晓或同意并开始投资后，则视为已仔细阅读该风险提示书并自愿承担投资市场带来的风险。请在参与投资之前务必详尽的了解投资的基本知识和相关风险，以及与交易中心有关的业务规则等，依法合规地进行投资。
          重要提示：<br/>
          　　1. 为了确保市场“公开、公平、公正”和健康稳定地发展，交易中心将采取更加严格的措施，加强市场的监管。请您务必密切地关注有关的系统公告、风险提醒等信息，及时了解市场风险状况，做到理性投资，切忌盲目跟风；<br/>　　2. 投资具有高投机性和高风险性，不适合利用养老基金、债务资金（如银行贷款）等进行投资的投资。只适合于满足以下全部条件的投资者： <br/><br/>　　a)年满18周岁以上并具有完全民事行为能力的公民或依法注册成立的企业法人或其他经济组织；<br/>　　b)能够充分理解有关于投资的一切风险，并且具有风险承受能力的投资者，年满60周岁并具有完全民事行为能力的公民须更为谨慎地评估风险承受能力和自身的身体健康状况；
          　　<br/>　　c)因投资失误而造成帐户资金部分或者全部损失、仍不会改变其正常生活方式或影响其正常生产经营的； 　　
          <br/>　　d)不具有交易中心开户管制相关制度规定的其他禁止开户情形。
          <br/><br/>　　3. 在开通交易服务前，请配合授权服务机构，完整、如实地提供开户所需要的信息，不得采取弄虚作假等手段规避有关的要求，否则，交易中心有权拒绝您开通交易服务。 　　
          <br/>　　4、如您已经注册并开始进行交易，即代表您已认可本《风险揭示书》的相关内
          <div class="risk-tips">相关风险提示</div>
          　　（一）法律、政策风险
          <br/>　　由于国家法律、法规、政策的变化，紧急措施的出台，相关监管部门监管措施的实施，交易中心交易规则的修改等原因，均可能会对您产生影响，（如：持仓限制、开仓数量限制等）导致本系统无法正常交易的情况，交易中心不承担任何责任和义务。
          　　
          <br/>　　（二）价格波动风险
          <br/>　　交易品种的价格受多种因素的影响，且影响机制非常复杂，投资价格以交易市场的价格为基础，交易中心会采取各种措施保证系统与交易市场价格保持同步，但实际操作中难以全面把握，因而存在出现投资失误的可能性，如果不能有效控制风险，则可能遭受较大的损失，客户应当了解并接受这种因参考其他交易市场价格波动而可能带来的投资风险。
          <br/>　　（三）技术风险
          <br/>　　1.
          本业务通过电子通讯技术和互联网技术来实现。有关通讯服务及软、硬件服务由不同的供应商提供，可能会存在品质和稳定性方面的风险；交易中心及授权服务机构不能控制电讯信号的强弱，也不能保证交易客户端的设备配置或者连接的稳定性以及互联网传播和接收的实时性。故由以上通讯或网络故障导致的某些服务中断或延时，可能会对您的投资产生影响。由于您电脑或者手机设备的故障以及网络数据传输等原因，系统指令可能会出现中断、延迟、停顿、数据错误，交易滑点等情况，不能完全保证所有的交易都严格在限定的参数内完成交易。
          　　
          <br/>　　2.
          您的电脑、操作系统等软硬件环境可能有由于自身原因或被病毒或网络黑客攻击，从而导致您的交易系统连接受到影响，使您的投资决策无法正确或及时执行。您利用本系统进行投资交易，应积极采取安全措施，如：定期维护手机及联网设备、采用防病毒及防黑客产品、妥善保管个人资料等，以防范电子化交易可能发生的各种风险
          　　
          <br/>　　3. 本业务使用的系统只是交易辅助工具，仅协助您按设定好的参数进行辅助开仓、平仓操作，并不能保证交易能够获利。
          　　
          <br/>　　4. 本业务的系统服务器与北京时间同步，请客户同步安装了客户端的机器时间。因为时间不同步可能导致交易的风险。
          　　
          <br/>　　5、本业务的系统提供了部分交易所未提供的交易方式，使用者应详细了解运行机制后方可使用，由此产生的交易结果均由使用者自行承担。
          　　
          对于上述不确定因素带来的风险，有可能会对您的投资产生影响，您应该充分了解并承担由此造成的全部损失。
          　　
          <br/>　　（四）交易风险
          　　
          <br/>　　1.
          您需要了解交易中心的交易业务具有低预付款比例和高杠杆比例的投资特点，可能会导致快速的盈利或亏损。若订单的方向与行情的波动相反，会造成较大的亏损，根据亏损的程度，您必须有条件满足随时追加预付款的要求，否则订单可能会被强行平仓，您必须承担由此造成的全部损失。
          <br/>　　2.
          本交易中心的价格以国际期货市场的价格为基础，连续报出交易中心的品种报价。该价格可能会与其他途径的报价存在微弱的差距，交易中心并不能保证上述交易价格与其他市场保持完全一致性。
          <br/>　　3. 您在交易系统中提交的市价单一经成交，即不可撤销，您必须接受这种方式可能带来的风险。
          <br/>　　4.
          交易中心禁止授权服务机构（及其分支机构）、授权服务机构经纪人及前述主体的工作人员与您分享收益或共同承担风险，或利用您的资金从事任何代客理财业务。授权服务机构（及其分支机构）、经纪人及前述主体的工作人员不会对您做出获利保证，您应知晓针对交易业务的任何获利保证或者不会发生亏损的承诺均为不实。
          <br/>　　5.您的投资交易必须是建立在自己的自主决定之上。交易中心、授权服务机构（及其分支机构）、经纪人及前述主体的工作人员提供的任何关于市场的分析和信息，仅供投资者参考，同时也不构成任何要约或承诺。由此而造成的交易风险由您自行承担。
          <br/>　　6. 在投资交易过程中，
          由于技术故障或其他不可抗力的因素，有可能出现偶然性的错误报价，
          交易中心可能事后会对错价及错价产生的盈亏作出纠正，由此而造成的交易风险由您自行承担。
          <br/>　　7.在电子交易过程中，由于技术故障或其他不可抗力因素，有可能出现偶然性的错误报价，交易市场有权在事后对错价及错价产生的盈亏作出纠正，客户应接受相关纠错行为，由此而造成的交易风险由客户承担。
          <br/>　　8.
          您对自行设置、保存和修改的相关密码负有保管义务。所有通过您账号及密码进行的系统操作，交易中心均视为您本人行为，因您密码的泄露、盗用或使用不当所产生的一切后果由您自行承担。
          对于上述不确定因素带来的风险，有可能会对您的投资产生影响，您应该充分了解并承担由此造成的全部损失。
          <br/>　　（五）不可抗力风险
          　　

          <br/>　　任何因交易中心不能够控制的原因，包括但不限于：地震、水灾、火灾、暴动、罢工、战争，政府管制、国际或国内的禁止，由于国家法律、行业规范、交易所规定（如：持仓限制、开仓数量限制等），或者限制以及停电、技术故障、电子故障等其它无法预测和防范的不可抗力事件，都有可能会对您的交易产生影响，您应该充分了解并承担由此造成的全部损失。
          　　
          <br/>　　由于网络科技的发展和进步日新月异，本《风险揭示书》上述风险告知事项仅为列举性质，未能详尽的列明有关交易的所有风险因素。

          <br/>　　因此，如果您不了解或者不能承受相关投资交易的风险，我们建议您不要开始交易。如果您申请或已使用系统开始交易，我们将认为您已经完全了解进行交易的相关风险，准备承受风险并准备承担由此带来的可能损失。

          <br/>　　请确认：以上《风险揭示书》，已经向本人（本单位）出示并说明，本人（本单位）已经认真阅读，完全理解和同意全部内容，自愿承担交易风险以及交易过程中可能产生的一切风险和损失。

        </div>
      </div>
    </van-dialog>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        riskShow: true,
      };
    },
    created(){
      // if(localStorage.getItem('LoginAccount')){
      //   this.$router.push({
      //     name:'home'
      //   })
      // }
    },
    methods:{
      openRisk(){
        this.riskShow = false
        // this.$router.push({
        //   name:'home'
        // })
      },
      closeRisk(){
        localStorage.removeItem('LoginAccount')
        this.Logout()
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../style/my";
</style>



